<template>
  <div class="flex flex-col flex-grow">
    <!-- Tabs -->
    <div class="flex overflow-x-auto py-2 px-4 border-b border-gray-200 bg-white sticky top-14 z-10" ref="tabButtonsContainer">
      <button 
        v-for="tab in tabs" 
        :key="tab.id"
        @click="switchTab(tab.id)"
        class="tab-button flex flex-col items-center whitespace-nowrap py-2 px-2 border-b-2 font-medium text-sm"
        :class="getTabButtonClass(tab.id)"
        :id="`tab-button-${tab.id}`"
        role="tab"
        :aria-controls="`panel-${tab.id}`"
        :aria-selected="tab.id === activeTabId ? 'true' : 'false'"
      >
        <span>{{ tab.name }}</span>
        <span v-if="tab.emoji" class="ml-1">{{ tab.emoji }}</span>
        <span class="tab-count text-xs text-gray-400 mt-1">
          {{ (currentSelectionOrders[tab.id] || []).length }} of {{ getTabItemCount(tab.id) }}
        </span>
      </button>
    </div>
    
    <!-- Tab Content -->
    <div class="flex-grow overflow-y-auto p-4 bg-gray-50" ref="tabPanelsContainer">
      <div 
        v-for="tab in tabs" 
        :key="`panel-${tab.id}`"
        :id="`panel-${tab.id}`"
        class="tab-panel"
        :class="{ 'hidden': tab.id !== activeTabId }"
        role="tabpanel"
        :aria-labelledby="`tab-button-${tab.id}`"
      >
        <h2 class="text-lg font-semibold text-gray-700 mb-4">
          {{ tab.id === 'eras' ? 'Select up to 3 Eras:' : `Select up to 3 Songs from ${tab.name}:` }}
        </h2>
        <div class="chip-container flex flex-wrap gap-3 mb-6" :data-tab-id="tab.id">
          <button 
            v-for="item in getSortedItemsForTab(tab.id)" 
            :key="`${tab.id}-${item}`"
            @click="toggleItemRank(item)"
            class="chip py-1 px-3 rounded-full text-sm"
            :class="getItemClass(item)"
            role="checkbox"
            :aria-checked="isItemRanked(item) ? 'true' : 'false'"
            tabindex="0"
            @keydown="handleChipKeyDown($event, item)"
          >
            <span v-if="isItemRanked(item)" class="chip-rank-indicator mr-1">
              {{ getRankNumber(item) }}
            </span>
            {{ item }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, ref, onMounted, watch } from 'vue';
import { MAX_SELECTION, erasWithSongs, eraNamesInOrder, originalSongLists, eraEmojis } from '../data';

export default {
  name: 'EditRankingsTabs',
  props: {
    activeTabId: {
      type: String,
      required: true
    },
    currentSelectionOrders: {
      type: Object,
      required: true
    },
    savedData: {
      type: Object,
      required: true
    }
  },
  emits: ['switch-tab', 'update:activeTabId'],
  setup(props, { emit }) {
    // DOM references
    const tabButtonsContainer = ref(null);
    const tabPanelsContainer = ref(null);
    const tabs = computed(() => {
      const result = [
        { id: 'eras', name: 'Eras', emoji: eraEmojis['eras'] }
      ];
      
      erasWithSongs.forEach(era => {
        result.push({
          id: era.id,
          name: era.name,
          emoji: era.emoji
        });
      });
      
      return result;
    });
    
    const currentItems = computed({
      get: () => {
        if (!props.currentSelectionOrders[props.activeTabId]) {
          props.currentSelectionOrders[props.activeTabId] = [];
        }
        return props.currentSelectionOrders[props.activeTabId];
      },
      set: (value) => {
        props.currentSelectionOrders[props.activeTabId] = value;
      }
    });
    
    const allItems = computed(() => {
      if (props.activeTabId === 'eras') {
        return [...eraNamesInOrder];
      } else {
        const era = erasWithSongs.find(e => e.id === props.activeTabId);
        if (era) {
          return [...era.songs];
        }
      }
      return [];
    });
    
    const sortedItems = computed(() => {
      return getSortedItemsForTab(props.activeTabId);
    });
    
    function getSortedItemsForTab(tabId) {
      // Get all items for the specified tab
      let items = [];
      
      if (tabId === 'eras') {
        items = [...eraNamesInOrder];
      } else {
        const era = erasWithSongs.find(e => e.id === tabId);
        if (era) {
          items = [...era.songs];
        }
      }
      
      // Get current selections for this tab
      const currentSelections = props.currentSelectionOrders[tabId] || [];
      
      // Sort items so that ranked items come first, in order of rank
      items.sort((a, b) => {
        const aRanked = currentSelections.includes(a);
        const bRanked = currentSelections.includes(b);
        
        if (aRanked && bRanked) {
          // Both items are ranked, sort by rank number
          return currentSelections.indexOf(a) - currentSelections.indexOf(b);
        } else if (aRanked) {
          // Only a is ranked, it should come first
          return -1;
        } else if (bRanked) {
          // Only b is ranked, it should come first
          return 1;
        } else {
          // Neither is ranked, maintain original order
          return 0;
        }
      });
      
      return items;
    }
    
    function isItemRanked(item) {
      const currentSelections = props.currentSelectionOrders[props.activeTabId] || [];
      return currentSelections.includes(item);
    }
    
    function getRankNumber(item) {
      const currentSelections = props.currentSelectionOrders[props.activeTabId] || [];
      return currentSelections.indexOf(item) + 1;
    }
    
    function getItemClass(item) {
      if (isItemRanked(item)) {
        return 'bg-blue-100 text-blue-800 border border-blue-200';
      } else {
        return 'bg-white border border-gray-200 text-gray-700';
      }
    }
    
    function toggleItemRank(item) {
      if (!props.currentSelectionOrders[props.activeTabId]) {
        props.currentSelectionOrders[props.activeTabId] = [];
      }
      
      const currentSelections = props.currentSelectionOrders[props.activeTabId];
      const index = currentSelections.indexOf(item);
      
      if (index !== -1) {
        // Item is already ranked, remove it
        currentSelections.splice(index, 1);
      } else if (currentSelections.length < MAX_SELECTION) {
        // Item is not ranked and we haven't reached the max selection
        currentSelections.push(item);
      }
      
      // Update tab button count
      updateTabButtonCount(props.activeTabId);
    }
    
    function getTabButtonClass(tabId) {
      return tabId === props.activeTabId 
        ? 'active border-blue-500 bg-blue-50 text-blue-600 font-semibold' 
        : 'border-transparent text-gray-500';
    }
    
    function getTabItemCount(tabId) {
      if (tabId === 'eras') {
        return eraNamesInOrder.length;
      } else {
        const era = erasWithSongs.find(e => e.id === tabId);
        return era ? era.songs.length : 0;
      }
    }
    
    function switchTab(tabId) {
      if (props.activeTabId === tabId) return;
      
      emit('switch-tab', tabId);
      emit('update:activeTabId', tabId);
    }
    
    function updateTabButtonCount(tabId) {
      // This is now handled reactively by the template
    }
    
    function handleChipKeyDown(event, item) {
      if (event.key === ' ' || event.key === 'Enter') {
        event.preventDefault();
        toggleItemRank(item);
      }
    }
    
    // Watch for activeTabId changes to ensure proper UI updates
    watch(() => props.activeTabId, (newTabId) => {
      // This would be where we'd do any additional processing when tabs change
    });
    
    return {
      tabButtonsContainer,
      tabPanelsContainer,
      tabs,
      sortedItems,
      maxSelection: MAX_SELECTION,
      isItemRanked,
      getRankNumber,
      getItemClass,
      toggleItemRank,
      getTabButtonClass,
      getTabItemCount,
      switchTab,
      getSortedItemsForTab,
      handleChipKeyDown
    };
  }
}
</script>
